@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow:wght@400;700&display=swap);
body {
  margin: 0;
  background: #333;
  font-family: "PT Sans Narrow",sans-serif;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
  width: 16px;
}

::-webkit-scrollbar-track {
  background: #222;
}


::-webkit-scrollbar-thumb {
  background: #555;
  border-radius: 15px;
  border: 3px solid transparent;
  background-clip: content-box;
}


::-webkit-scrollbar-thumb:hover {
  background: #666;
  border: 3px solid transparent;
  background-clip: content-box;
}
.App{
}

.card-list{
    width: 95vW;
    margin: 60px auto;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(240px,1fr));
    grid-gap:20px;
}
.card-border{
  box-sizing: border-box;
  padding: 7px;
  background: #776e39;
  border: 2px solid transparent;
  background: linear-gradient(70deg,#ccc, #333, #ccc);
  border-radius: 15px;
  transition:  0.25s ease-out;
  box-shadow: 0 0  5px 0px  rgba(0, 0, 0, 0.6);
}

.card-container{
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  /* align-items: center; */
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  background: #20282e;
  border-radius: 10px;
  padding:5px 15px;
  height: 360px;
  color:#eee;
}
.card-border:hover{
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  border:2px solid rgb(151, 246, 255);
  box-shadow: 0 10px  20px 0px  rgba(0, 0, 0, 0.3);
}

.title{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  margin-bottom: 5px;
 
}
.name{
  margin: 0;
  color: #eee;
  font-size: 1.3rem;
  font-weight: bold;
  text-transform: capitalize;
}
.id{
  font-weight: normal;
  font-size: .8rem;
  color:#ccc;
}
.hp{
  font-size: 1.2rem;
  color: rgb(255, 224, 138);
}


.image-container{
  background: radial-gradient( hsl(201, 20%, 28%),#172b35);
  min-height: 180px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 5px;
  border: 1px solid rgb(243, 232, 133);
  box-shadow: 0 0 15px rgb(0, 0, 0,.7);
  overflow: hidden;
}

.image-container img{
  max-width:65%;
  margin-bottom: -25px;
  max-height: 150px;
  -webkit-filter: drop-shadow(0px 0px 25px rgba(255, 217, 167, 0.5));
          filter: drop-shadow(0px 0px 25px rgba(255, 217, 167, 0.5));
}
