nav{
  position: fixed;
  top:0;
  background: #444444ee;
  height: 46px;
  width: 100%;
  z-index: 1;
  border-bottom: 2px solid #555;
}

.loading-container p{
  text-align: left;
  margin: 15px;
  color: white;
}
.loading-bar{
  position: absolute;
  bottom: 0;
  height: 3px;
  background: rgb(116, 201, 204);
  /* transition: 0.2s linear; */
}

.nav-container{
  box-sizing: border-box;
  margin: 0 auto;
  width: 95%;
  max-width: 340px;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
} 
input{
  height:35px;
  width: 80%;
  background: #2c2c2c;
  border: 1px solid #777;
  color: #ccc;
  border-radius: 5px;
  padding-left: 10px;
  font-size: 1.2rem;
  font-family: "PT Sans Narrow",sans-serif;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-shadow:inset 0 0 10px #00000099;
  
}
input:focus{
  background: #222;
  outline: none;
  border: 1px solid rgb(92, 174, 189);
}
input::-webkit-input-placeholder{
  text-align: center;
  font-size: 1rem;
}
input:-ms-input-placeholder{
  text-align: center;
  font-size: 1rem;
}
input::placeholder{
  text-align: center;
  font-size: 1rem;
}

select{
  height: 25px;
  margin-right: 10px;
  background: #333;
  border: 1px solid #777;
  color: #ccc;
  border-radius: 5px;
  padding:0 10px;
  font-size: 1rem;
  font-family: "PT Sans Narrow",sans-serif;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* box-shadow:inset 0 0 10px #00000099; */
}

select:focus{
  background: #333;
  outline: none;
  border: 1px solid rgb(92, 174, 189);
}

.loader{
  height: 90vh;
  width:100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.loader img { 
  width:64px;
  -webkit-animation: grow .5s ease-in-out alternate infinite;
          animation: grow .5s ease-in-out alternate infinite;
}


@-webkit-keyframes grow{
  0%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}


@keyframes grow{
  0%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}


